import { useSelector } from "react-redux"
import { Breadcrumb, Preloader } from "../../components"
import { contactService } from "../../services/contact.service"
import { useEffect } from "react"
import { useState } from "react"
import cogoToast from "@hasanm95/cogo-toast"
import IncomeContact from "../../components/Contact/ContactTransaction"
import { contactTransType } from "../../constants/contactTransType"



const Income = () => {
    const { currentUser } = useSelector(state => state.user)
    const [contactSales, setContactSales] = useState([])
    const [isLoading, setIsLoading] = useState(false)

    useEffect(() => {
        getUserContactSale()
    }, [])

    const getUserContactSale = async () => {
        try {
            setIsLoading(true)
            const sales = await contactService.getUserContactSales(currentUser._id)
            setContactSales(sales)
        } catch (error) {
            cogoToast.error("Cannot get your sales", { position: "top-center" });
        } finally {
            setIsLoading(false)
        }
    }

    return (
        <div className="body-wrapper space-pb--120 backgrounds__white-top">
            <Breadcrumb pageTitle="Income" />
            {
                isLoading
                    ?
                    <Preloader />
                    :
                    contactSales.map(contact => <IncomeContact key={contact._id} contact={contact} type={contactTransType.contactSale} />)
            }
        </div>
    )
}

export default Income