import { useNavigate } from "react-router-dom"
import { contactTransType } from "../../constants/contactTransType"
import { ConstTranslate } from "../../constants/translate"
import { utilService } from "../../services/util.service"
import Contact from "./Contact"

const ContactTransaction = ({ contact, type }) => {
    const navigate = useNavigate()

    const getContactSaleDate = () => {
        if (contact?.transactionHistory?.length) {
            const lastIdx = contact.transactionHistory.length - 1
            return utilService.getFormattedDate(new Date(contact.transactionHistory[lastIdx].createdAt))
        } else {
            return ConstTranslate.NO_DATE
        }
    }

    const onContact = () => {
        if (type === contactTransType.contactPurchase) {
            navigate(`/contact/${contact._id}`)
        }
    }

    return (
        <div className="" key={contact._id}>
            <Contact contact={contact} />
            {/* <div className="list-product__image">
                <img
                    src={contact?.img?.url || DUMMY_USER_URL}
                    className="img-fluid img br-15"
                    alt=""
                />
            </div>
            <div className="list-product__content">
                <p className="section">
                    {contact.category}
                </p>

                <p className="section">
                    {contact.company}
                </p>

                <p className="section">
                    {contact.desc}
                </p>

                <div className={`price${type === contactTransType.contactSale ? " sale" : ""}`}>
                    <span className="discounted-price">{`$${contact.price}`}</span>
                </div>

                <p className="section">
                    {getContactSaleDate()}
                </p>
            </div> */}
        </div>
    )
}

export default ContactTransaction