import { useState, useEffect } from "react"
import { addFavorite, deleteFavorite } from "../../store/user/user-action"
import { useDispatch, useSelector } from "react-redux"
import cogoToast from "@hasanm95/cogo-toast"

import { ReactComponent as FavoriteIcon } from "../../assets/icon/favorite.svg"
import { ReactComponent as ContactIcon } from "../../assets/icon/contact.svg"
import { addContactToCartAction } from "../../store/cart/cart-action"
import ContactInfo from "./ContactInfo"


const Contact = ({ contact }) => {
    const { currentUser } = useSelector(state => state.user)
    const [isFavorite, setIsFavorite] = useState(false)
    const dispatch = useDispatch()

    useEffect(() => {
        loadFavorite()
    }, [])

    const loadFavorite = () => {
        if (currentUser) {
            const isFavorite = currentUser.favorites.find(favorite => favorite._id === contact._id)
            if (isFavorite) setIsFavorite(true)
            else setIsFavorite(false)
        }
    }

    const onFavorite = async (item) => {
        try {
            if (isFavorite) {
                await deleteFavorite(dispatch, item, currentUser)
                setIsFavorite(false)
                cogoToast.success("Deleted from favorite", { position: "top-center" });
            }
            else {
                await addFavorite(dispatch, item, currentUser)
                setIsFavorite(true)
                cogoToast.success("Added to favorite", { position: "top-center" });
            }
        } catch (err) {
            cogoToast.error("Error", { position: "top-center" });
        }
    }

    const addToCart = () => {
        addContactToCartAction(dispatch, contact)
    }

    const isUserOwnContact = () => {
        const userContact = currentUser.contactTransactions.find(trans => trans.contact._id === contact._id)
        return userContact ? true : false
    }

    return (
        <div className="list-product border-bottom--medium" key={contact._id}>
            <ContactInfo contact={contact} />

            <div className="list-product__price">
                <h2>{contact.price}$</h2>
            </div>

            <div className="list-product__icon">
                {!isUserOwnContact() && <button className="icon" onClick={addToCart}>
                    <ContactIcon />
                </button>}

                <button className={`icon${isFavorite ? " active" : ""}`} onClick={() => onFavorite(contact)}>
                    <FavoriteIcon />
                </button>
            </div>
        </div>
    )
}

export default Contact